
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { api } from '@/utils/api.js';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
tabs: ["全部", "未使用", "已使用", "已过期"],
activeIndex: 0,
sliderOffset: 0,
sliderLeft: 0,
cou_list: "",
page: 2,
upLoading: false,
upLoadingComplete: false,
isnull: false,
nowShuoming: -1,

nvabarData: {
showCapsule: 1, //是否显示左上角图标
title: '优惠券', //导航栏 中间的标题
},
}
},
created() {

},
mounted() {
this.$nextTick(()=>{
this.sliderLeft = (this.$refs.tab.clientWidth / this.tabs.length - this.$refs.tabLine.clientWidth) / 2
})
this.getListcoupon();
},
methods: {
async getListcoupon(){
let data = {
plat: 3,
token: this.token,
account: this.account,
page: 1,
num: 10,
type: this.activeIndex
}
const [err, res] = await api.usercouponlist(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
if (res.data.page_size == 0) {
this.isnull = true;
this.cou_list = "";
} else {
for (var i = 0; i < res.data.list.length; i++) {
res.data.list[i].coupon_desc = res.data.list[i].coupon_desc.split("\\n");
}
this.cou_list = res.data.list;
this.isnull = false;
}
}
},
tabClick(e, index){
this.sliderOffset = e.currentTarget.offsetLeft;
this.activeIndex = index;
this.page = 2;
this.upLoadingComplete = false;
this.upLoading = false;
this.getListcoupon();
},
showShuoming(index){
if(window.isClick)return;
window.isClick = true;
if (this.nowShuoming == index) {
this.nowShuoming = -1;
} else {
this.nowShuoming = index;
}
setTimeout(() => {
window.isClick = false;
}, 100);
},
goUse(coupon_type){
if (coupon_type == 1) {
this.$router.push('haohuo');
} else if (coupon_type == 2) {
this.$router.push('allgoods');
}
}
},
computed: {
...mapGetters(['user', 'account', 'token'])
}
}



